@use "base/variables" as v;
@use "base/mixins" as m;


.citas{
    padding-left: 0;

    li{
        list-style: none;
        border-top: 1px solid v.$blanco;
        padding: .5rem .3rem;

        p{
            font-weight: bold;
            color:v.$azul;
        }

        span, 
        .servicio{
            font-weight: lighter;
            color:v.$blanco;
        }

        &:hover{
            background-color: darken(v.$negro, $amount: 30);
        }
    }
}

.servicios{
    @extend .citas;


    .acciones{
        margin: 0;

    }
    .boton,
    .boton-eliminar{
        margin: 0;
    }

}