.alerta{
    padding: 2rem;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;

    &.error{
        color: rgb(196, 17, 62);
        border: 2px solid crimson;
        margin-top: 1rem;

    }

    &.exito{
        color: rgb(54, 175, 37);
    }
}