@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app{
    @include m.tablet {
        display:  grid;
        grid-template-columns: repeat(2,1fr);
        min-height: 100vh;
    }

    .imagen{
        height: 18rem;
        background-image: url(../img/1.jpg);
        background-size: cover;
        background-position:center center ;
        @include m.tablet {
            height:auto;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet {
            overflow: hidden scroll;
            
        }
        &::-webkit-scrollbar{
            display: none;
        }
    }

    .nombre-pagina{
        text-align: center;
        margin: 5rem 0;
        font-weight: v.$bold;
    }

    .descripcion-pagina{
        text-align: center;
    }

    .barra{
        text-align: center;
        margin-top: 2rem;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
            
        }

        .boton{
            @include m.boton(v.$negro, v.$blanco);
            margin: 2rem 0;
            border: 1px solid crimson;
        }


    }

}

.barra-servicios{
    @include m.tablet {
        display:flex;
        justify-content: space-between;
    }
    .boton{
        margin: 2rem 0 4rem 0;
    }

}