@use "base/variables" as v;
@use "base/mixins" as m;

.listado-servicios{
    display: grid;
    gap: 2rem;
    @include m.tablet {
        @include m.grid(2,2rem);
        
    }

    .servicio{
        border-radius: 1rem;
        background-color: v.$blanco;
        transition-property: transform;
        transition-duration: 200ms;
        text-align: center;
        margin-right:  .5rem;
        margin-left: .5rem;
        &.seleccionado{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }

        &:hover{
            transform: scale(1.1);
        }

        p{
            color:v.$negro;
        }

        .precio-servicio{
            color: v.$azul;
            font-weight: bold;
            font-size: 2rem;
        }
    }



}