@use "base/variables" as v;
@use "base/mixins" as m;

.formulario{
    margin-top: 3rem;
    width: 100%;
}

.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label{
        flex: 0 0 10rem;
    }
    input{
        flex:1;
        border: none;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;

        &:disabled{
            background-color: lighten($color: #000000, $amount: 30);
            color: whitesmoke;
            font-weight: bold;
            cursor:not-allowed;
        }
    }

}